@import url(https://fonts.googleapis.com/css2?family=Comfortaa&display=swap);

body {
   
  background: linear-gradient(to bottom, #1d282e 0%,#1d2930 24%,#17252d 64%,#0e1f29 100%);
  margin: 0;
  color: #a6bac9;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0;
}

header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0;
}

footer{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 20px;
  border-top: 1px solid #677d90;
  background-color: #0b1920;
  color: #476b85;
}

input {
  background-color: #202b32;
  border: 5px solid #2f4858;
  border-radius: .3rem;
  color: #a6bac9;
  flex-grow: 1;
  font-size: 1.2rem;
  padding: 0.75rem 1rem;
  width: 55%;
  margin: 20px 0;
  transform: translateX(6%);
}

button {
  background-color: #39566a;
  border: 2px solid #1d2e38;
  border-radius: 2rem;
  flex-grow: 0;
  font-size: 1.2rem;
  line-height: 1;
  padding:  0.25rem 2rem;
  text-transform: uppercase;
  margin: 1rem 1rem;
}

@media screen and (max-width: 360px) {

  header{
    margin-top: 20px;
    display: flex;
    justify-content: center;
  }
  
  
  input {
    width:22%;
    margin: 2px 0;
  }
  
  button {
    border: 0;
  }
  
}

@media screen and (max-width: 419px) {

  header{
    margin-top: 20px;
    display: flex;
    justify-content: center;
  }
  
  
  input {
    width:61.5%;
    margin: 2px 0;
  }
  
  button {
    border: 0;
  }
  
}

@media screen and (min-width: 601px){
   
  nav {
    width:50%;
    transform: translateX(50%);
    position: fixed;
    z-index: 3000;
  }
  
  header {
    padding: 1rem 0 0 0;
  }
  
  input {
    width:100%;
    margin: 1px 20px 0 0;
    transform: translateX(-18%);
  }
  
  button {
    border: 0;
  }

}

.App {
  text-align: center;
}

.App-logo {
  height: 50vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 18s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.stations-gallery{
  display: flex;
  flex-direction: column;
  align-items: center;
  /* width: 90%; */
  margin: 0 20px 0 20px;
}

.station{
  border-radius: 8px;
  background-color: #21333f;
  box-shadow: -8px 10px 4.1px 0 rgba(0, 0, 0, 0.355);
  width: 88%;
  margin: 50px 50px;
  
}

.welcome{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.welcome-message{
  display: flex;
  justify-content: center;
}

.top-pick{
  width: 350px;
  border-radius: 8px;
  background-color: #21333f;
  box-shadow: -8px 10px 4.1px 0 rgba(0, 0, 0, 0.355);
  margin: 50px 50px;
}

.top-pick-gallery{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.radio-list{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.radio-button{
  display: flex;
  justify-content: center;
  align-items: center;
}

.brand{
  font-family: 'Comfortaa', cursive;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 150px;
}

.logo-app-name{
  font-size: 40px;
  transform: translateX(-5%);
}

.media-player{
  align-items: center;
}

.ResultCountry{
  margin: 12px 12px;
}

.ResultGenre{
  margin: 12px 12px;
}

.search-button{
  padding:  0.45rem 2rem;
  transform: translate(10%, 21.5%);
}

#result-country{
  padding: 3px 1px;
  border-radius: 12px;
  background-color:#1f2f3a;
  color: #a6bac9;
}

#result-genre{
  padding: 3px 1px;
  border-radius: 12px;
  background-color:#1f2f3a;
  color: #a6bac9;
}

.disclaimer{
  font-size: 10px;
}

.donate-button{
  color: #a6bac9;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 419px){  
  .App-logo {
    height: 40vmin;
  }

  .brand{
    transform: translateX(-3.5%);
  }
   
  .logo-app-name{
    font-size: 30px;
    transform: translateX(-10%);
  }
  
  .user-search{
    padding: 0 8px 0 8px;
    margin: 0;
    position: relative;
    transform: translate(-9%, 4%);
  }
  
  .ResultCountry{
    display: flex;
    flex-direction: column;
    transform: translate(2%, -190%);
  }
  
  .ResultGenre{
    display: flex;
    flex-direction: column;
    transform: translate(0%, -185%);
    margin-right: 160px;
  }
  
  .search-button{
    transform: translate(227%, -135%);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .welcome-message{
    transform:translateX(17.5%)
  }

  .top-pick{
    transform: translateY(-10%);
    width: 100%;
    
  }

  .top-pick-gallery{
    display: inline-block;
    transform: translate(-3%, -7%)

  }

  .disclaimer{
    transform: translateY(-600%);
    font-size: 9px;
  }

  
}

@media screen and (max-width: 360px){  

  .logo-app-name{
    font-size: 22px;
    transform: translateX(-25%)

  }
  
  .user-search{
    transform: translate(-3%, 4%);
  }
  
  .ResultCountry{
    transform: translate(3%, -205%);
  }
  
  .ResultGenre{
    transform: translate(6.3%, -185%);
  }
  
  .search-button{
    transform: translate(320%, 80%);
    padding: 10px;
  }

  .welcome-message{
    transform:translateX(19%)
  }

  .top-pick-gallery{
    display: inline-block;
    transform: translate(-14%, -7%)

  }

  .disclaimer{
    transform: translateY(-250%);
    font-size: 8px;
  }

  
}

@media screen and (min-width: 601px){
  .App-logo {
    height: 40vmin;
    pointer-events: none;
    width:  400px;
    height: 400px;
  }
  
  .brand{
    transform: translateX(-2.5%);
    margin-top: 130px; 
  }

  .logo-app-name{
    margin: 0;
    font-size: 65px;
  }
  
  .user-search{
    padding: 0 8px 0 8px;
    margin: 0;
    transform: translateY(30%);
  }
  
  .ResultCountry{
    transform: translate(-23%, -300%);
  }
  
  .ResultGenre{
    transform: translate(-23%, -300%);
  }
  
  .search-button{
    transform: translate(360%, -130.5%);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .disclaimer{
    font-size: 15px;
  }

  .station{
    margin: 25px 25px;
    width: 390px;
  }
  
  .stations-gallery{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; 
    justify-content: center;  
  }

  .top-pick{
    width: 390px;
  }

}

